<template>
  <div>
    <v-card class="mx-auto mt-2" max-width="750">
      <v-btn small depressed to="/prediction2">to2</v-btn>
      <div class="mt-5">Rounds</div>
    </v-card>

    <v-card class="mx-auto mb-2" outlined rounded max-width="750">
      <small class="ml-2">Total : {{ rounds.length }}</small>

      <div class="ml-2">
        <small>
          followPrev avg payout :
          {{ cToFixed(summary.followPrevSumPayout / summary.followPrevPayoutCount, 2) }}
        </small>
      </div>

      <div class="ml-2">
        <small>
          followPrev income :
          {{ cToFixed(summary.followPrevSumIncomeWin + summary.followPrevSumIncomeLose, 3) }} ({{
            cToFixed(summary.followPrevSumIncomeWin, 3)
          }}, {{ cToFixed(summary.followPrevSumIncomeLose, 3) }})</small
        >
      </div>

      <div class="ml-2">
        <small>
          followPrev : {{ summary.followPrevCount }}/{{ summary.followPrevWinCount }} ({{
            cToFixed((summary.followPrevWinCount / summary.followPrevCount) * 100, 2)
          }}% )
        </small>
      </div>

      <v-divider></v-divider>
      <div class="ml-2">
        <small>
          followPrev1 avg payout :
          {{ cToFixed(summary.followPrev1SumPayout / summary.followPrev1PayoutCount, 2) }}
          (remove narrow)
        </small>
      </div>

      <div class="ml-2">
        <small>
          followPrev1 income :
          {{ cToFixed(summary.followPrev1SumIncomeWin + summary.followPrev1SumIncomeLose, 3) }} ({{
            cToFixed(summary.followPrev1SumIncomeWin, 3)
          }}, {{ cToFixed(summary.followPrev1SumIncomeLose, 3) }})</small
        >
      </div>
      <div class="ml-2">
        <small>
          followPrev1 : {{ summary.followPrev1Count }}/{{ summary.followPrev1WinCount }} ({{
            cToFixed((summary.followPrev1WinCount / summary.followPrev1Count) * 100, 2)
          }}% )
        </small>
      </div>

      <v-row no-gutters class="pr-4 pl-1 mt-2">
        <small>Order</small>
      </v-row>
      <v-row no-gutters class="pr-1 pl-1">
        <v-col class="pr-1 pl-1">
          <small small class="text-center text--secondary">{{ amts_actual[0] }}</small>
        </v-col>
        <v-col class="pr-1 pl-1">
          <small small class="text-center text--secondary">{{ amts_actual[1] }}</small>
        </v-col>
        <v-col class="pr-1 pl-1">
          <small small class="text-center text--secondary">{{ amts_actual[2] }}</small>
        </v-col>
        <v-col class="pr-1 pl-1">
          <small small class="text-center text--secondary">{{ amts_actual[3] }}</small>
        </v-col>
        <v-col class="pr-1 pl-1">
          <small small class="text-center text--secondary">{{ amts_actual[4] }}</small>
        </v-col>
        <v-col class="pr-1 pl-1">
          <small small class="text-center text--secondary">{{ amts_actual[5] }}</small>
        </v-col>
      </v-row>
      <v-divider class="mt-2"></v-divider>
      <v-row no-gutters class="pr-1 pl-1 mt-2">
        <v-col class="pr-1 pl-1">
          <v-text-field
            v-model="amts_test1[0]"
            dense
            style="font-size: 12px"
            label=""
            placeholder=""
            @change="clickAmtTest"
          ></v-text-field>
        </v-col>
        <v-col class="pr-1 pl-1">
          <v-text-field
            v-model="amts_test1[1]"
            dense
            style="font-size: 12px"
            label=""
            placeholder=""
            @change="clickAmtTest"
          ></v-text-field>
        </v-col>
        <v-col class="pr-1 pl-1">
          <v-text-field
            v-model="amts_test1[2]"
            dense
            style="font-size: 12px"
            label=""
            placeholder=""
            @change="clickAmtTest"
          ></v-text-field>
        </v-col>
        <v-col class="pr-1 pl-1">
          <v-text-field
            v-model="amts_test1[3]"
            dense
            style="font-size: 12px"
            label=""
            placeholder=""
            @change="clickAmtTest"
          ></v-text-field>
        </v-col>
        <v-col class="pr-1 pl-1">
          <v-text-field
            v-model="amts_test1[4]"
            dense
            style="font-size: 12px"
            label=""
            placeholder=""
            @change="clickAmtTest"
          ></v-text-field>
        </v-col>
        <v-col class="pr-1 pl-1">
          <v-text-field
            v-model="amts_test1[5]"
            dense
            style="font-size: 12px"
            label=""
            placeholder=""
            @change="clickAmtTest"
          ></v-text-field>
        </v-col>
      </v-row>

      <v-row class="ma-4 mt-2">
        <v-btn x-small depressed class="" @click="clickApplyTest(1, amts_test1)">Apply test1</v-btn>
      </v-row>
      <v-divider></v-divider>
    </v-card>

    <v-card class="mx-auto" outlined rounded max-width="750">
      <v-data-table
        mobile-breakpoint="200"
        hide-default-footer
        :items-per-page="-1"
        :headers="roundsHeaders"
        :items="rounds"
        must-sort
        dense
      >
        <template v-slot:item.position="{ item }">
          <v-chip
            class="pa-1"
            :color="winColor(item.up_down, item.position, item.position_index)"
            x-small
          >
            {{ item.position }}
          </v-chip>
        </template>

        <template v-slot:item.followPrev1="{ item }">
          <v-chip class="pa-1" :color="winColor(item.up_down, item.followPrev1, 1)" x-small>
            {{ item.followPrev1 }}
          </v-chip>
        </template>

        <template v-slot:item.up_down="{ item }">
          <!-- <v-chip class="pa-1" :color="upDownColor(item)" x-small> -->
          <v-chip class="pa-1" :color="upDownColor(item)" x-small>
            {{ item.up_down }}
          </v-chip>
        </template>

        <template v-slot:item.amt="{ item }">
          <small>{{ item.amt }}</small>
        </template>

        <template v-slot:item.epoch="{ item }">
          <small>{{ item.epoch }}</small>
        </template>

        <template v-slot:item.rsi="{ item }">
          <small>{{ item.rsi }}</small>
        </template>

        <template v-slot:item.ema1="{ item }">
          <small>{{ item.ema1 }}</small>
        </template>

        <template v-slot:item.ema2="{ item }">
          <small>{{ item.ema2 }}</small>
        </template>

        <template v-slot:item.price="{ item }">
          <small>{{ item.price.toFixed(2) }}</small>
        </template>
        <template v-slot:item.payout="{ item }">
          <small>{{ cToFixed(item.payout, 2) }}</small>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>

<script>
import HttpService from '@/services/HttpService';

export default {
  data: () => ({
    roundsHeaders: [
      { text: 'epoch', value: 'epoch' },
      { text: 'ud', value: 'up_down' },
      { text: 'followPrev', value: 'position' },
      { text: 'followPrev1', value: 'followPrev1' },
      { text: 'rsi', value: 'rsi' },
      { text: 'ema1', value: 'ema1' },
      { text: 'ema2', value: 'ema2' },
      { text: 'amt', value: 'amt' },
      { text: 'pay', value: 'payout' },
    ],

    indexHeaders: [
      { text: '', value: 's' },
      { text: '1', value: 'i1' },
      { text: '2', value: 'i2' },
      { text: '3', value: 'i3' },
      { text: '4', value: 'i4' },
      { text: '5', value: 'i5' },
      { text: '6', value: 'i6' },
      { text: '7', value: 'i7' },
    ],

    amts_test1: [],
    amts_test1_reverse: [],
    amts_test2: [],
    amts_actual: [],
    amts_actual_reverse: [],

    rounds: [],
    settings: {},

    summary: { followWinCount: 0 },
  }),

  created: function () {
    this.setAmts();
    this.init();
    this.getSettings();
  },

  methods: {
    async init() {
      // let rounds = await HttpService.get('/pish/prediction/rounds')
      let rounds = await HttpService.getPlain('https://api.delog.io/api/v1/prediction/rounds');

      //   let amtTrackers = await HttpService.get('/pish/prediction/amttracker');

      // console.log(amtTrackers)

      this.summary.positionWinCount = 0;
      this.summary.positionCount = 0;

      this.summary.followWinCount = 0;
      this.summary.upCount = 0;
      this.summary.downCount = 0;

      this.summary.winCount = [
        { s: 'order', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0, i9: 0 },
        { s: 'order', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0, i9: 0 },
        { s: 'adv', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0, i9: 0 },
        { s: 'adv', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0, i9: 0 },
        { s: 'final', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0, i9: 0 },
        { s: 'final', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0, i9: 0 },
      ];

      let newRounds = [];

      for (let item of rounds) {
        if (item.hash) {
          item.hashShort = item.hash.substr(0, 5);
        }

        if (item.up_down === 'UP') {
          this.summary.upCount++;
        } else {
          this.summary.downCount++;
        }

        item.total_amt = item.bull_amt + item.bear_amt;
        item.price = item.close_price - item.lock_price;

        // plain
        if (item.position === 'UP' || item.position === 'DOWN') {
          this.summary.positionCount++;
          if (item.up_down === item.position) {
            this.summary.positionWinCount++;
          }
        }

        if (item.position === item.up_down) {
          let objKey = 'i' + item.order_index;
          if (item.position === 'UP' || item.position === 'DOWN') {
            this.summary.winCount[0][objKey]++;
          }
        } else {
          let objKey = 'i' + item.order_index;
          if (item.position === 'UP' || item.position === 'DOWN') {
            this.summary.winCount[1][objKey]++;
          }
        }

        newRounds.push(item);
      }

      //   newRounds = this.testRealIncAmt(newRounds, amtTrackers);

      newRounds = this.testFollowPrev(newRounds);
      newRounds = this.testFollowPrev1(newRounds);

      newRounds = this.testFollowWinIndex(newRounds);
      newRounds = this.testAdv(newRounds);

      newRounds = this.testFinal(newRounds);

      this.testAmtProfit(newRounds);

      console.log(this.summary.followWinCountList);
      this.rounds = newRounds;
    },

    async setAmts() {
      this.amts_test1 = [];
      this.amts_test1_reverse = [];
      this.amts_test2 = [];
      this.amts_actual = [];
      this.amts_actual_reverse = [];

      let amts = await HttpService.get('/pish/prediction/amts');
      for (let item of amts) {
        if (item.name === 'index1') {
          this.amts_actual.push(item.i1);
          this.amts_actual.push(item.i2);
          this.amts_actual.push(item.i3);
          this.amts_actual.push(item.i4);
          this.amts_actual.push(item.i5);
          this.amts_actual.push(item.i6);
          this.amts_actual.push(item.i7);
          this.amts_actual.push(item.i8);
        } else if (item.name === 'index1_test1') {
          this.amts_test1.push(item.i1);
          this.amts_test1.push(item.i2);
          this.amts_test1.push(item.i3);
          this.amts_test1.push(item.i4);
          this.amts_test1.push(item.i5);
          this.amts_test1.push(item.i6);
          this.amts_test1.push(item.i7);
          this.amts_test1.push(item.i8);
        } else if (item.name === 'index1_test2') {
          this.amts_test2.push(item.i1);
          this.amts_test2.push(item.i2);
          this.amts_test2.push(item.i3);
          this.amts_test2.push(item.i4);
          this.amts_test2.push(item.i5);
          this.amts_test2.push(item.i6);
          this.amts_test2.push(item.i7);
          this.amts_test2.push(item.i8);
        } else if (item.name === 'index1_reverse_test1') {
          this.amts_test1_reverse.push(item.i1);
          this.amts_test1_reverse.push(item.i2);
          this.amts_test1_reverse.push(item.i3);
          this.amts_test1_reverse.push(item.i4);
          this.amts_test1_reverse.push(item.i5);
          this.amts_test1_reverse.push(item.i6);
          this.amts_test1_reverse.push(item.i7);
          this.amts_test1_reverse.push(item.i8);
        } else if (item.name === 'index1_reverse') {
          this.amts_actual_reverse.push(item.i1);
          this.amts_actual_reverse.push(item.i2);
          this.amts_actual_reverse.push(item.i3);
          this.amts_actual_reverse.push(item.i4);
          this.amts_actual_reverse.push(item.i5);
          this.amts_actual_reverse.push(item.i6);
          this.amts_actual_reverse.push(item.i7);
          this.amts_actual_reverse.push(item.i8);
        }
      }
    },

    async getSettings() {
      let settings = await HttpService.get('/pish/prediction/rounds/settings');
      this.settings = settings[0];
      console.log(settings);
    },

    testAmtProfit(rounds) {
      this.summary.followPrevSumPayout = 0;
      this.summary.followPrevPayoutCount = 0;
      this.summary.followPrevSumIncomeWin = 0;
      this.summary.followPrevSumIncomeLose = 0;

      this.summary.followPrev1SumPayout = 0;
      this.summary.followPrev1PayoutCount = 0;
      this.summary.followPrev1SumIncomeWin = 0;
      this.summary.followPrev1SumIncomeLose = 0;

      //   let amts_test1 = [0.005, 0.006, 0.0137, 0.0277, 0, 0, 0, 0, 0];
      let amtAmount = 0.005;

      for (let item of rounds) {
        let fee = 0.00047;
        item.amt = amtAmount;
        if (item.up_down === item.position) {
          let payout = 0;

          if (item.up_down === 'UP') {
            item.payout = item.bear_amt / item.bull_amt + 1;
            payout = item.bear_amt / item.bull_amt + 1;
          } else {
            item.payout = item.bull_amt / item.bear_amt + 1;
            payout = item.bull_amt / item.bear_amt + 1;
          }

          item.income = item.amt * payout * 0.97 - item.amt;
          if (item.income > 0) item.income += fee;
          this.summary.followPrevSumIncomeWin += item.income;

          if (item.amt > 0) {
            this.summary.followPrevSumPayout += payout * 0.97;
            this.summary.followPrevPayoutCount++;
          }
        } else {
          item.income = -item.amt;
          if (item.income > 0) item.income += fee;
          this.summary.followPrevSumIncomeLose += item.income;
        }

        if (item.followPrev1 === 'UP' || item.followPrev1 === 'DOWN') {
          if (item.up_down === item.followPrev1) {
            let payout = 0;

            if (item.up_down === 'UP') {
              item.payout = item.bear_amt / item.bull_amt + 1;
              payout = item.bear_amt / item.bull_amt + 1;
            } else {
              item.payout = item.bull_amt / item.bear_amt + 1;
              payout = item.bull_amt / item.bear_amt + 1;
            }

            item.income = item.amt * payout * 0.97 - item.amt;
            if (item.income > 0) item.income += fee;
            this.summary.followPrev1SumIncomeWin += item.income;

            if (item.amt > 0) {
              this.summary.followPrev1SumPayout += payout * 0.97;
              this.summary.followPrev1PayoutCount++;
            }
          } else {
            item.income = -item.amt;
            if (item.income > 0) item.income += fee;
            this.summary.followPrev1SumIncomeLose += item.income;
          }
        }
      }
    },

    testRealIncAmt(rounds, amtTrackers) {
      this.summary.followIncAdvWinCount = 0;
      this.summary.followIncAdvCount = 0;
      this.summary.followIncWinCount = 0;
      this.summary.followIncCount = 0;
      this.summary.followIncWinCount2 = 0;
      this.summary.followIncCount2 = 0;

      for (let item of rounds) {
        let findItem = this.$underscore.find(amtTrackers, {
          epoch: item.epoch,
        });
        if (findItem) {
          item.bear_amt0 = findItem.bear_amt0;
          item.bear_amt1 = findItem.bear_amt1;
          item.bear_amt2 = findItem.bear_amt2;
          item.bear_amt3 = findItem.bear_amt3;
          item.bull_amt0 = findItem.bull_amt0;
          item.bull_amt1 = findItem.bull_amt1;
          item.bull_amt2 = findItem.bull_amt2;
          item.bull_amt3 = findItem.bull_amt3;
        }
      }

      for (let i = rounds.length - 3; i >= 0; i--) {
        //plain
        if (rounds[i + 2].up_down === rounds[i + 2].position_follow_inc) {
          rounds[i].follow_inc_win_index = 1;
        } else if (
          rounds[i + 2].position_follow_inc === 'UP' ||
          rounds[i + 2].position_follow_inc === 'DOWN'
        ) {
          rounds[i].follow_inc_win_index = rounds[i + 2].follow_inc_win_index + 1;
        } else {
          rounds[i].follow_inc_win_index = rounds[i + 2].follow_inc_win_index;
        }
        //2
        if (rounds[i + 2].up_down === rounds[i + 2].position_follow_inc2) {
          rounds[i].follow_inc_win_index2 = 1;
        } else if (
          rounds[i + 2].position_follow_inc2 === 'UP' ||
          rounds[i + 2].position_follow_inc2 === 'DOWN'
        ) {
          rounds[i].follow_inc_win_index2 = rounds[i + 2].follow_inc_win_index2 + 1;
        } else {
          rounds[i].follow_inc_win_index2 = rounds[i + 2].follow_inc_win_index2;
        }

        // if (rounds[i + 2].position_follow_inc2 === rounds[i + 2].up_down) {
        // 	let objKey = 'i' + rounds[i + 2].follow_inc_win_index2
        // 	if (rounds[i + 2].position_follow_inc2 === 'UP' || rounds[i + 2].position_follow_inc2 === 'DOWN') {
        // 		this.summary.winCount[4][objKey]++
        // 	}
        // } else {
        // 	let objKey = 'i' + rounds[i + 2].follow_inc_win_index2
        // 	if (rounds[i + 2].position_follow_inc2 === 'UP' || rounds[i + 2].position_follow_inc2 === 'DOWN') {
        // 		this.summary.winCount[5][objKey]++
        // 	}
        // }
      }

      return rounds;
    },

    testFinal(rounds) {
      this.summary.testWinCount = 0;
      this.summary.testCount = 0;

      for (let i = rounds.length - 1; i >= 0; i--) {
        let position = '';

        if (rounds[i].position_index_adv === 1) {
          position = rounds[i].position_adv;
        } else if (rounds[i].position_index_adv === 2) {
          position = rounds[i].position_adv;
        } else if (rounds[i].position_index_adv === 6) {
          position = rounds[i].position_adv;
        }

        rounds[i].position_1 = position;
      }

      for (let i = rounds.length - 11; i >= 0; i--) {
        let frame = this.getFrame(rounds, i + 2, 10);
        let findItem1 = [];
        let targetEpoch = rounds[i].epoch % 2;

        for (let item of frame) {
          if (
            item &&
            (item.position_1 === 'UP' || item.position_1 === 'DOWN') &&
            item.epoch % 2 === targetEpoch
          ) {
            findItem1.push(item);
          }
        }

        findItem1 = findItem1[0];
        if (findItem1 !== undefined) {
          if (findItem1.position_1 === 'UP' || findItem1.position_1 === 'DOWN') {
            if (findItem1.up_down === findItem1.position_1) {
              rounds[i].position_index_1 = 1;
            } else {
              rounds[i].position_index_1 = findItem1.position_index_1 + 1;
            }
          } else {
            rounds[i].position_index_1 = findItem1.position_index_1;
          }
        }
      }

      for (let i = rounds.length - 3; i >= 0; i--) {
        if (rounds[i + 2].position_1 === 'UP' || rounds[i + 2].position_1 === 'DOWN') {
          this.summary.testCount++;
          if (rounds[i + 2].up_down === rounds[i + 2].position_1) {
            this.summary.testWinCount++;
            let objKey = 'i' + rounds[i + 2].position_index_1;
            this.summary.winCount[4][objKey]++;
          }
          if (rounds[i + 2].up_down !== rounds[i + 2].position_1) {
            let objKey = 'i' + rounds[i + 2].position_index_1;
            this.summary.winCount[5][objKey]++;
          }
        }
      }

      for (let i = 1; i < 6; i++) {
        let objKey = 'i' + i;
        let percent =
          (this.summary.winCount[4][objKey] /
            (this.summary.winCount[4][objKey] + this.summary.winCount[5][objKey])) *
          100;
        percent = percent.toFixed(2);
        this.summary.winCount[4][objKey] = this.summary.winCount[4][objKey] + ' (' + percent + ')';
      }

      return rounds;
    },

    testAdv(rounds) {
      this.summary.reverseWinCount = 0;
      this.summary.reverseCount = 0;

      for (let i = rounds.length - 1; i >= 0; i--) {
        let position = '';

        let bullGap30 = rounds[i].bull_amt3 - rounds[i].bull_amt0;
        let bullGap31 = rounds[i].bull_amt3 - rounds[i].bull_amt1;
        let bullGap32 = rounds[i].bull_amt3 - rounds[i].bull_amt2;
        let bearGap30 = rounds[i].bear_amt3 - rounds[i].bear_amt0;
        let bearGap31 = rounds[i].bear_amt3 - rounds[i].bear_amt1;
        let bearGap32 = rounds[i].bear_amt3 - rounds[i].bear_amt2;

        // if (rounds[i].bull_amt3 > rounds[i].bear_amt3) {
        // 	if (rounds[i].bull_amt2 > rounds[i].bear_amt2) {
        // 		if (rounds[i].bull_amt0 > rounds[i].bear_amt0) {
        // 			position = 'UP'
        // 		}
        // 	}
        // }

        if (bearGap32 > bullGap31) {
          position = 'DOWN';
        }

        // if (rounds[i].bear_amt3 > rounds[i].bull_amt3) {
        // 	if (rounds[i].bear_amt2 > rounds[i].bull_amt2) {
        // 		if (rounds[i].bear_amt0 > rounds[i].bull_amt0) {
        // 			position = 'DOWN'
        // 		}
        // 	}
        // }

        if (bullGap32 > bearGap31) {
          position = 'UP';
        }
        rounds[i].position_adv = position;
      }

      for (let i = rounds.length - 3; i >= 0; i--) {
        if (rounds[i + 2].position_adv === 'UP' || rounds[i + 2].position_adv === 'DOWN') {
          if (rounds[i + 2].up_down === rounds[i + 2].position_adv) {
            rounds[i].position_index_adv = 1;
          } else {
            rounds[i].position_index_adv = rounds[i + 2].position_index_adv + 1;
          }
        } else {
          rounds[i].position_index_adv = rounds[i + 2].position_index_adv;
        }

        if (rounds[i + 2].position_adv === 'UP' || rounds[i + 2].position_adv === 'DOWN') {
          this.summary.reverseCount++;
          if (rounds[i + 2].up_down === rounds[i + 2].position_adv) {
            this.summary.reverseWinCount++;
            let objKey = 'i' + rounds[i + 2].position_index_adv;
            this.summary.winCount[2][objKey]++;
          }
          if (rounds[i + 2].up_down !== rounds[i + 2].position_adv) {
            let objKey = 'i' + rounds[i + 2].position_index_adv;
            this.summary.winCount[3][objKey]++;
          }
        }
      }

      for (let i = 1; i < 6; i++) {
        let objKey = 'i' + i;
        let percent =
          (this.summary.winCount[2][objKey] /
            (this.summary.winCount[2][objKey] + this.summary.winCount[3][objKey])) *
          100;
        percent = percent.toFixed(2);
        this.summary.winCount[2][objKey] = this.summary.winCount[2][objKey] + ' (' + percent + ')';
      }

      return rounds;
    },

    testFollowWinIndex(rounds) {
      this.summary.followWinCount = 0;
      this.summary.followCount = 0;

      for (let i = rounds.length - 3; i >= 0; i--) {
        let position = '';

        if (rounds[i + 2].bull_amt > 0) {
          position = rounds[i + 2].bull_amt > rounds[i + 2].bear_amt ? 'UP' : 'DOWN';
          rounds[i + 2].position_follow = position;
        }

        if (position === 'UP' || position === 'DOWN') {
          this.summary.followCount++;
        }

        if (rounds[i + 2].up_down === position) {
          rounds[i].follow_win_index = 1;
          this.summary.followWinCount++;
        } else if (position === 'UP' || position === 'DOWN') {
          rounds[i].follow_win_index = rounds[i + 2].follow_win_index + 1;
        } else {
          rounds[i].follow_win_index = rounds[i + 2].follow_win_index;
        }
      }
      return rounds;
    },

    testFollowPrev(rounds) {
      this.summary.followPrevWinCount = 0;
      this.summary.followPrevCount = 0;

      for (let i = rounds.length - 1; i >= 0; i--) {
        this.summary.followPrevCount++;
        if (rounds[i].up_down === rounds[i].position) {
          this.summary.followPrevWinCount++;
        }
      }
      return rounds;
    },

    testFollowPrev1(rounds) {
      this.summary.followPrev1WinCount = 0;
      this.summary.followPrev1Count = 0;

      for (let i = rounds.length - 1; i >= 0; i--) {
        rounds[i].followPrev1 = rounds[i].position;

        const abs = Math.abs(rounds[i].ema1 - rounds[i].ema2);

        if (abs < rounds[i].ema1 * 0.0001) {
          rounds[i].followPrev1 = 'narrow';
        }

        if (rounds[i].followPrev1 === 'UP' || rounds[i].followPrev1 === 'DOWN') {
          this.summary.followPrev1Count++;
        }

        if (rounds[i].up_down === rounds[i].followPrev1) {
          this.summary.followPrev1WinCount++;
        }
      }
      return rounds;
    },

    getFrame(rounds, start, size) {
      let list = [];
      for (let i = start; i < start + size; i++) {
        list.push(rounds[i]);
      }
      return list;
    },

    clickAmtTest() {
      this.testAmtProfit(this.rounds);
    },

    async clickUpdateSetting() {
      await HttpService.put('/pish/prediction/rounds/settings', this.settings);
    },

    async clickApplyTest(nameNum, amtTest) {
      let numList = amtTest.map((x) => Number(x));
      let name = '';
      if (nameNum === 1) {
        name = 'index1_test1';
      } else if (nameNum === 2) {
        name = 'index1_test2';
      } else if (nameNum === 3) {
        name = 'index1_reverse_test1';
      }

      await HttpService.put('/pish/prediction/amts', { name: name, amt_list: numList });
      this.setAmts();
    },

    getPercent(total, count) {
      return ((count / total) * 100).toFixed(1);
    },

    winColor(up_down, position, index) {
      let color = 'white lighten-3';

      if (up_down === position) {
        if (position === 'UP') {
          if (index >= 7) {
            color = 'green lighten-1';
          } else if (index >= 6) {
            color = 'green lighten-2';
          } else if (index >= 5) {
            color = 'green lighten-3';
          } else if (index >= 3) {
            color = 'green lighten-4';
          } else if (index >= 1) {
            color = 'green lighten-4';
          }
        } else if (position === 'DOWN') {
          if (index >= 7) {
            color = 'red lighten-1';
          } else if (index >= 6) {
            color = 'red lighten-2';
          } else if (index >= 5) {
            color = 'red lighten-3';
          } else if (index >= 3) {
            color = 'red lighten-4';
          } else if (index >= 1) {
            color = 'red lighten-4';
          }
        }
      }

      return color;
    },

    upDownColor(item) {
      let color = 'grey lighten-5';
      if (item.up_down === 'UP') {
        color = 'green lighten-5';
      } else if (item.up_down === 'DOWN') {
        color = 'red lighten-5';
      } else if (item.up_down === 'BURN') {
        color = 'amber lighten-5';
      }
      return color;
    },

    cToFixed(num, size) {
      try {
        return num.toFixed(size);
      } catch (e) {
        return '';
      }
    },
  },
};
</script>
